@import '../../../scss/breakpoints.scss';

.site-footer {
  .newsletter {
    background-color: var(--accent-2-color);
  }
  footer.footer-section {
  .footer-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 2vw;
    @include to-sm() {
      gap: 40px;
    }
    .logo-wrapper {
      width: 300px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-right: 5vw;
      @include to-md() {
        width: 220px;
      }
      @include to-sm() {
        width: 100%;
        gap: 20px;
      }
      .logo-container {
        width: 100%;
        margin-bottom: 20px;
        @include to-sm() {
          width: 70%;
          margin: 0 auto;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      .socials {
        align-items: start;
        @include to-sm() {
          width: 80%;
          margin: 0 auto;
        }
        a {
          color: var(--text-alternate-color);
          &:hover {
            color: var(--accent-1-color);
          }
        }
      }
    }
    nav {
      flex: 1;
      display: flex;
      justify-content: space-between;
      gap: 30px;
      flex-wrap: wrap;
      @include to-sm() {
        justify-content: center;
      }
      .link-title {
        font-size: 14px;
        color: #fff;
      }
      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        margin-top: 3px;
        li {
          padding-top: 3px;
          a {
            white-space: nowrap;
            font-size: 14px;
            color: #fff;
          }
        }
      }
    }
  }
  }
  .footer-bar {
    background-color: var(--secondary-color);
    .footer-bar-wrapper {
      display: flex;
      justify-content: space-between;
      padding: 8px 0;
      gap: 10px;
      flex-wrap: wrap;
      @include to-xs() {
        justify-content: center;
      }
      * {
        font-size: 14px!important;
      }
    }
  }
}